.button {
  background-color: blue;
  height: 40px;
  padding: 0 10px;
  border-radius: 5px;
  width: 100%;
  font-size: 16px;
  color: white;
  font-weight: bold;
  cursor: pointer;
  border: none;
  transition: all 0.5s ease;
}

.add-task-button-container {
  margin-left: 10px;
  flex: 1;
}

.button:hover {
  background-color: white;
  color: blue;
}
